
import React from 'react';
import styled from 'styled-components';
import './Gallery.css';

const GalleryContainer = styled.div`
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
`;

const ImageContainer = styled.div`
  position: relative;
  margin: 10px;
  &:hover .description {
    opacity: 1;
  }
`;

const Image = styled.img`
  width: 300px;
  height: 200px;
  object-fit: cover;
`;

const Description = styled.div`
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  width: 100%;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
`;

const UploadForm = styled.form`
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
`;

const UploadInput = styled.input`
  margin-bottom: 10px;
`;

const Gallery = () => {
  const [images, setImages] = useState([
    { src: 'public\Images\Graduation Ceremony.jpg', description: 'Graduation Ceremony' },
    { src: 'public\Images\University Hallway.jpg', description: 'University Hallway' },
    { src: 'public\Images\Alumni Meet.jpg', description: 'Alumni Community' },
  ]);

  const [newImage, setNewImage] = useState({ src: '', description: '' });

  const handleUpload = (e) => {
    e.preventDefault();
    setImages([...images, newImage]);
    setNewImage({ src: '', description: '' });
  };

  return (
    <div>
      <UploadForm onSubmit={handleUpload}>
        <UploadInput
          type="text"
          placeholder="Image URL"
          value={newImage.src}
          onChange={(e) => setNewImage({ ...newImage, src: e.target.value })}
          required
        />
        <UploadInput
          type="text"
          placeholder="Image Description"
          value={newImage.description}
          onChange={(e) => setNewImage({ ...newImage, description: e.target.value })}
          required
        />
        <button type="submit">Upload Image</button>
      </UploadForm>
      <GalleryContainer>
        {images.map((image, index) => (
          <ImageContainer key={index}>
            <Image src={image.src} alt={image.description} />
            <Description className="description">{image.description}</Description>
          </ImageContainer>
        ))}
      </GalleryContainer>
    </div>
  );
};

export default Gallery;
